import React, {useContext} from 'react';
// contexi ishletmek ucun themecontexti import etmeliyik providerin icinde yazilandan sonra artiq harda cagrsaq ishleyecek
// contextle daha etrafli izahi Navbar.withcontext.js de
// Digerlerini yoxlamq ucun navbarwithdekileri bura kopyala at
import {ThemeContext} from "../Contexts/ThemeContext";
import { AuthContext } from "../Contexts/AuthContext";


const  Navbar = () =>{
    // console.log(this.context)
    const { isLightTheme , light , dark } = useContext(ThemeContext);
    const theme = isLightTheme ? light : dark ;
    const { isAuthenticated , toggleAuth } = useContext(AuthContext);
    return(
        <nav style={{background : theme.ui ,  color : theme.syntax }} >
            <h1>Context api</h1>
            <div onClick={toggleAuth} >
                {isAuthenticated ? 'LogOut' : 'LogIn'}
            </div>
            <ul>
                <li>Ana sehife</li>
                <li>Haqqimizda</li>
                <li>Elaqe</li>
            </ul>
        </nav>
    );
}


export default Navbar;
