import React, {useContext, useState} from 'react';
import  { ThemeContext } from "../Contexts/ThemeContext";
import {BookContext} from '../Contexts/BookContext';
import uuid from  'uuid/v1';
import NewBookForm from './NewBookForm'
// HOOK ILE ISTIFADE UCUN CLASS OLAN BOOKLISTI FUNCTION ELEDIK VE USE CONTEXT ISTIFADE ELEDIK

const BookList = () =>{
    const { isLightTheme , light , dark } = useContext(ThemeContext);
    const theme = isLightTheme ? light : dark ;
    const [message , setmessage]= useState('KITAB ELAVE EDIN')
    const {books , setBooks} = useContext(BookContext)
    const Addnew = (title)=>{
      if (title !=''){
          setBooks([
              ...books , { title: title  , id : uuid() }
          ])
          setmessage('KITAB MUVEFFEQIYETLE ELAVE EDILDI');
      }
      else {
          setmessage('KITAB ADINI XANAYA DAXIL EDIN');
      }
    }
    return(
        <div className="book-list" style={{background : theme.bg ,  color : theme.syntax }}>
            <ul>
                {
                    books.map(book => {
                       return(
                           <li key={book.id} style={{background : theme.ui }} >{book.title}</li>
                       )
                    })
                }

            </ul>
            <h5>{message}</h5>
          <NewBookForm Addnew={Addnew} />
        </div>
    );
}

export default BookList;
